body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #cadcf9;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --primary-color: #1016FF;
  --accent-color: #ffd110;
  --border-color: #777;
  --max-width: 1000px;
  --min-width: 625px;
}

.clear {
  clear: both;
}

.App {
  text-align: center;
  width: 100%;
  margin: auto;
  max-width: 1000px;
  max-width: var(--max-width);
  min-width: 625px;
  min-width: var(--min-width);
  background: #fff;
  min-height: 500px;
  border: 1px solid #333;
  /* border-left: 1px solid black;
  border-bottom: 1px solid black;
  border-right: 1px solid black; */
  font-family: 'helvetica';
  box-sizing: border-box;
  margin-bottom: 10px;
  margin-top: 5px;
}

.header {
  min-width: 625px;
  min-width: var(--min-width);
  background: #080B7F;
  padding-top: 15px;
  padding-bottom: 15px;
}

.header a {
  text-decoration: none;
}

#header-menu {
  max-width: 1000px;
  max-width: var(--max-width);
  width: 100%;
  margin: auto;
  list-style-type: none;
  font-size: 1.5rem;
  padding: 0px;
}

#header-menu li {
  display: inline;
  padding: 10px;
  color: #ccc;
  margin-right: 20px;
}

#header-menu a.selected li{
  border-bottom: 3px solid #ffd110;
  border-bottom: 3px solid var(--accent-color);
  color: #ffd110;
  color: var(--accent-color);
}

#header-menu li:hover {
  background: #5c61ff;
  color: #ffd110;
  color: var(--accent-color);
  border-bottom: 3px solid #ffd110;
  border-bottom: 3px solid var(--accent-color);
}
.home {
    text-align: left;
    padding: 10px;
}

.standings {
    width: 100%;
    border-bottom: 1px solid black;
    padding-bottom: 10px;
}

.terms p {
    margin-left: 20px;
    margin-right: 20px;
    line-height: 1.5em;
}

.terms h4 {
    margin-left: 10px;
}

.terms ul {
    text-decoration: none;
    line-height: 1.3em;
    margin-left: 20px;
}

.clear {
    clear: both;
}
.standings-grid {
    margin-bottom: 25px;
}

.standings-grid-row {
    width: 100%;
    font-size: 0.9em;
    line-height: 1.5em;
}

.standings-grid-row:hover {
    background: #ddd;
}

.standings-grid-header {
    font-size: 1em;
    font-weight: bold;
    height: 30px;
    border-bottom: 1px solid var(--border-color);
}

.standings-grid-header:hover {
    background: #fff;
}

.standings-grid-row-label {
    width: 10%;
    min-width: 60px;
    height: 100%;
    float: left;
    box-sizing: border-box;
    border-right: 1px solid var(--border-color);
    padding-left: 10px;
    font-weight: bold;
    font-size: 1.1em;
}

.standings-grid-header .standings-grid-row-label {
    font-size: 1.3em;
    padding-left: 0px;
    color: var(--primary-color);
}

.standings-grid-row-record {
    width: 30%;
    min-width: 180px;
    height: 100%;
    float: left;
    box-sizing: border-box;
    /* padding-top: 5px; */
    border-right: 1px solid var(--border-color);
}


.standings-grid-row-record span {
    width: 25%;
    display: inline-block;
    text-align: center;
}

.standings-grid-row-opponents {
    width: 60%;
    min-width: 360px;
    float: left;
    box-sizing: border-box;
    /* padding-top: 3px; */
    padding-left: 10px;
}

.standings-grid-row-opponents .opp-label{
    width: 5%;
    display: inline-block;
    font-weight: normal;
    color: #000;
}

.standings-grid-row-opponents ul {
    list-style-type: none;
    display: inline;
    padding: 0px;
}

.standings-grid-row-opponents li {
    display: inline-block;
    width: 15%;
    text-align: center;
}

.standingsTable {
    width: 100%
}

.standingsTable th {
    font-size: 0.9em;
    text-align: center;
    border-bottom: 1px solid var(--border-color);
}

.standingsTable td {
    text-align: center;
}

.standingsTable .name {
    width: 20%;
    border-right: 1px solid var(--border-color);
    padding: 5px;
    min-width: 60px;
    text-align: left;
}

.standingsTable th.name {
    font-size: 1.2em;
}

.standingsTable .win,
.standingsTable .loss,
.standingsTable .ties {
    width: 5%;
}

.standingsTable .vsAdam,
.standingsTable .vsBrad,
.standingsTable .vsBrian,
.standingsTable .vsChad,
.standingsTable .vsMike,
.standingsTable .vsUndrafted {
    width: 10%;
}
