:root {
  --primary-color: #1016FF;
  --accent-color: #ffd110;
  --border-color: #777;
  --max-width: 1000px;
  --min-width: 625px;
}

.clear {
  clear: both;
}

.App {
  text-align: center;
  width: 100%;
  margin: auto;
  max-width: var(--max-width);
  min-width: var(--min-width);
  background: #fff;
  min-height: 500px;
  border: 1px solid #333;
  /* border-left: 1px solid black;
  border-bottom: 1px solid black;
  border-right: 1px solid black; */
  font-family: 'helvetica';
  box-sizing: border-box;
  margin-bottom: 10px;
  margin-top: 5px;
}

.header {
  min-width: var(--min-width);
  background: #080B7F;
  padding-top: 15px;
  padding-bottom: 15px;
}

.header a {
  text-decoration: none;
}

#header-menu {
  max-width: var(--max-width);
  width: 100%;
  margin: auto;
  list-style-type: none;
  font-size: 1.5rem;
  padding: 0px;
}

#header-menu li {
  display: inline;
  padding: 10px;
  color: #ccc;
  margin-right: 20px;
}

#header-menu a.selected li{
  border-bottom: 3px solid var(--accent-color);
  color: var(--accent-color);
}

#header-menu li:hover {
  background: #5c61ff;
  color: var(--accent-color);
  border-bottom: 3px solid var(--accent-color);
}