.standings-grid {
    margin-bottom: 25px;
}

.standings-grid-row {
    width: 100%;
    font-size: 0.9em;
    line-height: 1.5em;
}

.standings-grid-row:hover {
    background: #ddd;
}

.standings-grid-header {
    font-size: 1em;
    font-weight: bold;
    height: 30px;
    border-bottom: 1px solid var(--border-color);
}

.standings-grid-header:hover {
    background: #fff;
}

.standings-grid-row-label {
    width: 10%;
    min-width: 60px;
    height: 100%;
    float: left;
    box-sizing: border-box;
    border-right: 1px solid var(--border-color);
    padding-left: 10px;
    font-weight: bold;
    font-size: 1.1em;
}

.standings-grid-header .standings-grid-row-label {
    font-size: 1.3em;
    padding-left: 0px;
    color: var(--primary-color);
}

.standings-grid-row-record {
    width: 30%;
    min-width: 180px;
    height: 100%;
    float: left;
    box-sizing: border-box;
    /* padding-top: 5px; */
    border-right: 1px solid var(--border-color);
}


.standings-grid-row-record span {
    width: 25%;
    display: inline-block;
    text-align: center;
}

.standings-grid-row-opponents {
    width: 60%;
    min-width: 360px;
    float: left;
    box-sizing: border-box;
    /* padding-top: 3px; */
    padding-left: 10px;
}

.standings-grid-row-opponents .opp-label{
    width: 5%;
    display: inline-block;
    font-weight: normal;
    color: #000;
}

.standings-grid-row-opponents ul {
    list-style-type: none;
    display: inline;
    padding: 0px;
}

.standings-grid-row-opponents li {
    display: inline-block;
    width: 15%;
    text-align: center;
}

.standingsTable {
    width: 100%
}

.standingsTable th {
    font-size: 0.9em;
    text-align: center;
    border-bottom: 1px solid var(--border-color);
}

.standingsTable td {
    text-align: center;
}

.standingsTable .name {
    width: 20%;
    border-right: 1px solid var(--border-color);
    padding: 5px;
    min-width: 60px;
    text-align: left;
}

.standingsTable th.name {
    font-size: 1.2em;
}

.standingsTable .win,
.standingsTable .loss,
.standingsTable .ties {
    width: 5%;
}

.standingsTable .vsAdam,
.standingsTable .vsBrad,
.standingsTable .vsBrian,
.standingsTable .vsChad,
.standingsTable .vsMike,
.standingsTable .vsUndrafted {
    width: 10%;
}